import XLSX from "xlsx";

export default function createCsv(dataSource) {
  const data = JSON.parse(JSON.stringify(dataSource));

  /*   const fileName = `ExportedData`;
          const exportType = `${fileType}`; */

  // export json to Worksheet of Excel
  // only array possible
  const WSData = XLSX.utils.json_to_sheet(data);

  // A workbook is the name given to an Excel file
  var wb = XLSX.utils.book_new(); // make Workbook of Excel

  // add Worksheet to Workbook
  // Workbook contains one or more worksheets
  XLSX.utils.book_append_sheet(wb, WSData, "ExportedData"); // sheetAName is name of Worksheet

  // export Excel file
  XLSX.writeFile(wb, "exportedData.xlsx"); // name of the file is 'book.xlsx'
}
