<template>
    <v-menu bottom origin="center center" transition="scale-transition">    
        <v-btn slot="activator" color="tfggreen" class="mt-3 ml-0" dark round small>    
            Export    
        </v-btn>     
        <v-list dense>    
            <v-list-tile @click="ExportPDFFile">   
                <v-list-tile-title>PDF</v-list-tile-title>  
            </v-list-tile>    
            <v-list-tile @click="ExportCSVFile">   
                <v-list-tile-title>CSV</v-list-tile-title>  
            </v-list-tile> 
            <v-list-tile @click="ExportXLSFile">   
                <v-list-tile-title>XLS</v-list-tile-title>  
            </v-list-tile> 
        </v-list>    
    </v-menu>
</template>

<script>
import exportToPdf from "../services/createPDF";
import exportToCsv from "../services/createCsv";
import exportToXLSX from "../services/createXLSX";

export default {
  props: ["data", "headers", "colStyles"],
  methods: {
    ExportPDFFile() {
      let newHeaderArr = this.headers.map(val => {
        return {
          title: val.text,
          dataKey: val.value
        };
      });
      exportToPdf(newHeaderArr, this.data, this.colStyles);
    },
    ExportCSVFile() {
      exportToCsv(this.data, "csv");
    },
    ExportXLSFile() {
      exportToXLSX(this.data);
    }
  }
};
</script>
